<template>
<div class="userStat">
    <selectThree :search="search">
    </selectThree>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="userstat" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectThree from "@/components/selectThree"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'userStat',
    data () {
        return {
            msg: '',
            isLoading: false,
            usersStatBar:"",
        usersStatPie:"",
        usersStatPie2:"",
        usersStatName:""
        }
    },
    components:{
        Loading,
        selectThree
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
        olt(){
            return this.$store.getters.getOLT
        }
    },
    methods:{
    usersStatChart(self){
        let myChart = echarts.init(document.getElementById('userstat'));
        myChart.clear();
        let option = {
            color: ['#A0CE3A', '#31C5C0', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {top: '10%',left:"5%",right:"50%"},
            legend: {
                    orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:self.usersStatPie
                },
            xAxis : [
                {
                    type : 'category',
                    data : self.usersStatBar
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: "用户数"
                }
            ],
            series : [
                {
                    name:'用户数',
                    type:'bar',
                    data:self.usersStatPie,
                    itemStyle: {
                        normal: {
                    　　 label: {
                    　　　　　　show:true,
                    　　　　　　position: "top",
                    　　　　　　textStyle: {
                    　　　　　　color: "#333"
                        　　　　},
                        　　　　formatter: function(params) {
                            　　　　　　if(params.data.rate){
                            　　　　　　　　return "IPTV渗透率\n"+params.data.rate+"%" 
                            　　　　　　}else{
                                        console.log(params)
                            　　　　　　　　return "";
                            　　　　　　}
                        　　　　}
                        }
                        }
                    }
                },
                {
                    name:'PON口数',
                    type:'pie',
                    selectedMode: 'single',
                    radius: [0, '20%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            position: 'inner'
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    data:self.usersStatPie
                },
                {
                    name:'型号',
                    type:'pie',
                    radius: ['25%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: ' {b|{b}}\n{hr|}\n {c}  {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20,
                                    align: 'center'
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2
                                }
                            }
                        }
                    },
                    data:self.usersStatPie2
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createUsersStat(data){
        this.usersStatBar=[]
        this.usersStatPie=[]
        this.usersStatPie2=[]
        for (let i=0;i<data.length;i++){
            let temppie={
                'name': data[i].package_speed,
                'value': data[i].cnt,
                'rate':(data[i].iptv_cnt/data[i].cnt*100).toFixed(2)
            }
            this.usersStatPie.push(temppie)
            let temppie2={
                'name': data[i].package_speed+'iptv',
                'value': data[i].iptv_cnt
            }
            this.usersStatPie2.push(temppie2)
        }
        let datalist = this.usersStatPie.sort((a, b) => a.value - b.value);
            for (let i=0;i<datalist.length;i++){
                this.usersStatBar.push(datalist[i].name)
            }
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/pon-port-users-stat/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station,
                    olt:this.olt
				}
			})
            .then((response) =>{
                this.isLoading=false
                this._createUsersStat(response.data.data.count_list)
                
                this.usersStatChart(this)
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


