<template>
<div class="downstreamTraffic">
    <selectFour :search="search">
    </selectFour>
    <div id="downstreamTraffic" style="height:500px;margin-top:20px"></div>
    <!-- <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition> -->
</div>
</template>

<script>
import selectFour from "@/components/selectFour"
import Loading from '@/components/loading'
import echarts from 'echarts'
import axios from 'axios'
export default {
    name:"downstreamTraffic",
    components:{
        selectFour
    },
methods:{
    search(){
        this.initchart()
    },
    initchart(){
        let myChart = echarts.init(document.getElementById('downstreamTraffic'));
        myChart.clear();
        let option = {
            title: {
                text: '人均下行流量-时间曲线'
            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data:["上行流量均值"]
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            toolbox: {
                feature: {
                    saveAsImage: {}
                }
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: ['周一','周二','周三','周四','周五','周六','周日']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name:'上行流量均值',
                    type:'line',
                    data:[12.9, 32.2,20.8, 34.4, 27.3, 23.8, 21.0]
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
}
}
</script>

<style>

</style>



