<template>
<div class="serviceBoardStat">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="pon_rate_chart2" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'serviceBoardStat',
    data () {
        return {
            msg: '',
            isLoading: false,
            SBrate:0,
            SBSlotstatPie:[],
            SBSlotstatBar:[],
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
        // olt(){
        //     return this.$store.getters.getOLT
        // }
    },
    methods:{
    ponTypeChart(datapie,databar){
        let myChart = echarts.init(document.getElementById('pon_rate_chart2'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {top: '10%',left:"5%",right:"50%"},
            legend: {
                    orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:datapie
                },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : databar[0],

                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name:"OLT数"
                }
            ],
            series : [
                {
                    name:'OLT数',
                    type:'bar',
                    data:databar[1],
                },
                {
                    name:'OLT数',
                    type:'pie',
                    radius: ['20%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: ' {b|{b}}\n {hr|}\n {c}  {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20,
                                    align:'center'
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2
                                }
                            }
                        }
                    },
                    data:datapie
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createSBSlotStat(data){
        this.SBSlotstatPie=[]
        this.SBSlotstatBar=[]
        this.SBSlotstatPie=[{
            name:'0-20%',
            value:data['0-20']
        },{
            name:'20-40%',
            value:data['20-40']
        },{
            name:'40-60%',
            value:data['40-60']
        },{
            name:'60-80%',
            value:data['60-80']
        },{
            name:'80-100%',
            value:data['80-100']
        }]
        let tempx=['0-20%','20-40%','40-60%','60-80%','80-100%']
        let tempy=[data['0-20'],data['20-40'],data['40-60'],data['60-80'],data['80-100']]
        this.SBSlotstatBar.push(tempx)
        this.SBSlotstatBar.push(tempy)
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/service-board-stat/',{
				params:{
                    city:this.city,
                    department:this.department ? this.department.slice(0,-3)+"通信分公司" : this.department,
                    station:this.station,
                    // olt:this.olt
				}
			})
            .then((response) =>{
                this.SBrate=response.data.data.rate;
            })
            .catch(function (error) {
                console.log(error)
            });
            axios.get('/api/service-board-slot-stat/',{
				params:{
                    city:this.city,
                    department:this.department ? this.department.slice(0,-3)+"通信分公司" : this.department,
                    station:this.station,
                    // olt:this.olt
				}
			})
            .then((response) =>{
                this._createSBSlotStat(response.data.data)
                this.isLoading=false
                this.ponTypeChart(this.SBSlotstatPie,this.SBSlotstatBar)
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


