<template>
<div class="portcount">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="upperchart1" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'portcount',
    data () {
        return {
            msg: '',
            isLoading: false,
            uplinkportcount:0,
        portratepie:[],
        portratebar:[],
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
    ponCountchart(self){
        let myChart = echarts.init(document.getElementById('upperchart1'));
        myChart.clear();
        let option = {
            color: ['#A0CE3A', '#31C5C0', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {top: '10%',left:"5%",right:"50%"},
            legend: {orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:self.portratepie
                    },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : this.portratebar[0],
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: 'OLT数量',
                }
            ],
            series : [
                {
                    name:'OLT数',
                    type:'bar',
                    data:self.portratebar[1],
                },
                {
                    name:'OLT数',
                    type:'pie',
                    radius: ['20%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                a: {
                                    color: '#999',
                                    lineHeight: 18,
                                    align: 'center'
                                },
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2
                                }
                            }
                        }
                    },
                    data:self.portratepie
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createPonCount(data){
        this.portratepie=[]
        this.portratebar=[]
        let tempx=[]
        let tempy=[]
        for (let i=0;i<data.length;i++){
            let temp={
                'name': data[i].bandwidth,
                'value': data[i].cnt
            }
            this.portratepie.push(temp)
            tempx.push(data[i].bandwidth)
            tempy.push(data[i].cnt)
        }
        this.portratebar.push(tempx)
        this.portratebar.push(tempy)
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/olt-uplink/10ge/count/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this.uplinkportcount=response.data.data['10GE_olt_count']+response.data.data['others']
            })
            .catch(function (error) {
                console.log(error)
            });
            axios.get('/api/olt-uplink-port-count/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this._createPonCount(response.data.data)
                this.isLoading=false
                this.ponCountchart(this)
            })
            .catch(function (error) {
                console.log(error)
            });
            
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


