import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state={//要设置的全局访问的state对象
     selectMenu: "",
     city:"天津",
     department:"",
     station:"",
     olt:"",
     timelist:[],
     //要设置的初始属性值
   };
const getters = {   //实时监听state值的变化(最新状态)
    getSelectMenu(state) {  //承载变化的showFooter的值
       return state.selectMenu
    },
    getCity(state) {  //承载变化的showFooter的值
        return state.city
     },
     getDepartment(state) {  //承载变化的showFooter的值
        return state.department
     },
     getStation(state) {  //承载变化的showFooter的值
        return state.station
     },
     getOLT(state) {  //承载变化的showFooter的值
        return state.olt
     },
     getTimeList(state) {  //承载变化的showFooter的值
        return state.timelist
     },
    //  getStationList(state) {  //承载变化的showFooter的值
    //     return state.stationlist
    //  },
    //  getOLTList(state) {  //承载变化的showFooter的值
    //     return state.oltlist
    //  },
};
const mutations = {
    // show(state) {   //自定义改变state初始值的方法，这里面的参数除了state之外还可以再传额外的参数(变量或对象);
    //     state.showFooter = true;
    // },
    // hide(state) {  //同上
    //     state.showFooter = false;
    // },
    setSelectMenu(state,menu){ //同上，这里面的参数除了state之外还传了需要增加的值sum
       state.selectMenu=menu
    },
    setCity(state,_city){ //同上，这里面的参数除了state之外还传了需要增加的值sum
        state.city=_city
     },
     setDepartment(state,_department){ //同上，这里面的参数除了state之外还传了需要增加的值sum
        state.department=_department
     },
     setStation(state,_station){ //同上，这里面的参数除了state之外还传了需要增加的值sum
        state.station=_station
     },
     setOLT(state,_OLT){ //同上，这里面的参数除了state之外还传了需要增加的值sum
        state.olt=_OLT
     },
     setTimeList(state,_timelist){ //同上，这里面的参数除了state之外还传了需要增加的值sum
        state.timelist=_timelist
     },
    //  setStationList(state,_stationlist){ //同上，这里面的参数除了state之外还传了需要增加的值sum
    //     state.stationlist=_stationlist
    //  },
    //  setOLTList(state,_OLTlist){ //同上，这里面的参数除了state之外还传了需要增加的值sum
    //     state.oltlist=_OLTlist
    //  },
};
 const actions = {
    // hideFooter(context) {  //自定义触发mutations里函数的方法，context与store 实例具有相同方法和属性
    //     context.commit('hide');
    // },
    // showFooter(context) {  //同上注释
    //     context.commit('show');
    // },
    getNewMenu(context,menu){   //同上注释，num为要变化的形参
        context.commit('setSelectMenu',menu)
     },
     getNewCity(context,_city){   //同上注释，num为要变化的形参
        context.commit('setCity',_city)
     },
     getNewDepartment(context,_department){   //同上注释，num为要变化的形参
        context.commit('setDepartment',_department)
     },
     getNewStation(context,_station){   //同上注释，num为要变化的形参
        context.commit('setStation',_station)
     },
     getNewOLT(context,_OLT){   //同上注释，num为要变化的形参
        context.commit('setOLT',_OLT)
     },
     getNewTimeList(context,_timelist){   //同上注释，num为要变化的形参
        context.commit('setTimeList',_timelist)
     },
    //  getNewStationList(context,_stationlist){   //同上注释，num为要变化的形参
    //     context.commit('setStationList',_stationlist)
    //  },
    //  getNewOLTList(context,_OLTlist){   //同上注释，num为要变化的形参
    //     context.commit('setOLTList',_OLTlist)
    //  },
};
const store = new Vuex.Store({
       state,
       getters,
       mutations,
       actions
});
export default store;


