<template>
<div class="averageTrafficRate">
    <selectFour :search="search">
    </selectFour>
    <div id="averageTrafficRate" style="height:500px;margin-top:20px"></div>
    <!-- <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition> -->
</div>
</template>

<script>
import selectFour from "@/components/selectFour"
import Loading from '@/components/loading'
import echarts from 'echarts'
import axios from 'axios'
export default {
    name:"averageTrafficRate",
    components:{
        selectFour
    },
methods:{
    search(){
        this.initchart()
    },
    initchart(){
        let myChart = echarts.init(document.getElementById('averageTrafficRate'));
        myChart.clear();
        let option = {
            title: {
                text: '人均上下行流量-时间曲线'
            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data:["上行流量均值","下行流量均值"]
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            toolbox: {
                feature: {
                    saveAsImage: {}
                }
            },
            xAxis: {
                type: 'category',
                data: ['周一','周二','周三','周四','周五','周六','周日']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name:'上行流量均值',
                    type:'line',
                    data:[12.9, 32.2,20.8, 34.4, 27.3, 23.8, 21.0]
                },
                {
                    name:'下行流量均值',
                    type:'line',
                    data:[14.9, 22.2,24.8, 24.4, 17.3, 13.8, 31.0]
                },
                {
                    name:'下行流量均值',
                    type:'bar',
                    data:[2.9, 2.2,0.8, 4.4, 2.3, 3.8, 1.0]
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
}
}
</script>

<style>

</style>



