<template>
    <div class="app-container">
        <div class="app-side app-side-left">
            <sidebar></sidebar>
        </div>
        <div class='app-right' style="overflow-y: auto; position:relative">
            <router-view name='appRight'></router-view>
        </div>
    </div>
</template>

<script>
import sidebar from '@/components/sidebar'
import oltcount from '@/components/trafficAnalysis'
export default {
    components:{
        sidebar,
        oltcount
    }

}
</script>

<style>
.app-container{
position: relative;
padding:0;
margin:0;
height: calc(100% - 95px);
width:100%;
display: flex;
}
.app-side {
    height: 100%;
    width:250px;
    font-weight: 700;
    color:#333;
    background-color: #fff;
    /* border-right: solid 1px grey; */
    overflow-y: scroll;
    
}
.app-right {
    width: calc(100% - 230px);
    height: calc(100% - 40px);
    padding:20px;
    overflow-y: scroll;
}
</style>



