<template>
<div class="vendorPonType">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="pon_rate_chart4" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'vendorPonType',
    data () {
        return {
            msg: '',
            isLoading: false,
            vendorpontypepie:[],
            vendorpontypepie2:[],
            vendorpontypebar:[],
            vendorpontypename:[],
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
    initChart2(self){
        let myChart = echarts.init(document.getElementById('pon_rate_chart4'));
        myChart.clear();
        let option = {
            color: ['#A0CE3A', '#31C5C0', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {top: '10%',left:"5%",right:"50%"},
            legend: {
                    orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:self.vendorpontypepie
                },
            // legend: {
            //     orient:'horizontal',
            //     top: "center",
            //     right: "right",
            //     textStyle: {
            //         color: '#333',
            //         fontSize: 14,
            //     },
            //     // tslint:disable-next-line:max-line-length
            //     data: self.vendorpontypename
            // },
            // toolbox: {
            //     feature: {
            //         saveAsImage: {show: true,backgroundColor: '#fff'},
            //         myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
            //             show:true,//是否显示    
            //             title:'切换饼图', //鼠标移动上去显示的文字    
            //             icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
            //             onclick:function() {//点击事件,这里的option1是chart的option信息    
            //                 myChart.clear();
            //                 let option2 = {
            //                     color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            //                     backgroundColor: '#fff',
            //                     tooltip: {
            //                         trigger: 'item',
            //                         formatter: "{b}<br/> {c} ({d}%)"
            //                     },
            //                     legend: {
            //                         orient: 'vertical',
            //                         x: 'left',
            //                         data:self.vendorpontypepie
            //                     },
            //                     toolbox: {
            //                             feature: {
            //                                 saveAsImage: {show: true,backgroundColor: '#fff'},
            //                                 myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
            //                                     show:true,//是否显示    
            //                                     title:'切换柱状图', //鼠标移动上去显示的文字    
            //                                     icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
            //                                     onclick:function() {//点击事件,这里的option1是chart的option信息    
            //                                         myChart.clear();
            //                                         myChart.setOption(option);
            //                                     }
            //                                 }
            //                             }
            //                         },
            //                     series: [
                                    
            //                     ]
            //                 };
            //                 myChart.setOption(option2);
            //             }    
            //        },
                   
            //     }
            // },
            xAxis : [
                {
                    type : 'category',
                    data : ['厂家制式']
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name : "端口数"
                }
            ],
            series: self.vendorpontypebar
        };
        myChart.setOption(option);
    },
    _createVendorPonType(data){
        this.vendorpontypepie=[]
        this.vendorpontypepie2=[]
        this.vendorpontypebar=[]
        this.vendorpontypename=[]
        for (let i=0;i<data.length;i++){
            let temps={
                    name: data[i]['manufacturer']+'1GE',
                    type: 'bar',
                    stack: data[i]['manufacturer'],
                    label: {
                        normal: {
                            show: true,
                            position: 'insideRight'
                        }
                    },
                    data: [data[i]['1GE_count']]
                }
            let temp2={
                    name: data[i]['manufacturer']+'10GE',
                    type: 'bar',
                    stack: data[i]['manufacturer'],
                    label: {
                        normal: {
                            show: true,
                            position: 'insideRight'
                        }
                    },
                    data: [data[i]['10GE_count']]
                }
            this.vendorpontypebar.push(temps)
            this.vendorpontypebar.push(temp2)
            this.vendorpontypename.push(data[i]['manufacturer']+'1GE')
            this.vendorpontypename.push(data[i]['manufacturer']+'10GE')
            let temppie={
                'name': data[i]['manufacturer'],
                'value': data[i]['10GE_count']+ data[i]['1GE_count']
            }
            this.vendorpontypepie.push(temppie)
            let temppie2={
                'name': data[i]['manufacturer']+'1GE',
                'value': data[i]['1GE_count']
            }
            let temppie3={
                'name': data[i]['manufacturer']+'10GE',
                'value': data[i]['10GE_count']
            }
            this.vendorpontypepie2.push(temppie2)
            this.vendorpontypepie2.push(temppie3)
        }
        let tempseries ={
                                        name:'PON口数',
                                        type:'pie',
                                        selectedMode: 'single',
                                        radius: [0, '20%'],
                                        center: ['75%', '50%'],
                                        label: {
                                            normal: {
                                                position: 'inner'
                                            }
                                        },
                                        labelLine: {
                                            normal: {
                                                show: false
                                            }
                                        },
                                        data:this.vendorpontypepie
                                    }
            let tempseries2 ={
                                        name:'型号',
                                        type:'pie',
                                        radius: ['25%', '35%'],
                                        center: ['75%', '50%'],
                                        label: {
                                            normal: {
                                                formatter: '{b|{b}}\n{hr|}\n {c}  {per|{d}%}  ',
                                                backgroundColor: '#eee',
                                                borderColor: '#aaa',
                                                borderWidth: 1,
                                                borderRadius: 4,
                                                rich: {
                                                    hr: {
                                                        borderColor: '#aaa',
                                                        width: '100%',
                                                        borderWidth: 0.5,
                                                        height: 0
                                                    },
                                                    b: {
                                                        fontSize: 14,
                                                        lineHeight: 20,
                                                        align: 'center'
                                                    },
                                                    per: {
                                                        color: '#eee',
                                                        backgroundColor: '#334455',
                                                        padding: [2, 4],
                                                        borderRadius: 2
                                                    }
                                                }
                                            }
                                        },
                                        data:this.vendorpontypepie2
                                    }
            this.vendorpontypebar.push(tempseries)
            this.vendorpontypebar.push(tempseries2)
        //待添加
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/vendor-pon-type/',{
				params:{
                    city:this.city,
                    department:this.department ? this.department.slice(0,-3)+"通信分公司" : this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this._createVendorPonType(response.data.data)
                this.isLoading=false
                this.initChart2(this) 
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


