<template>
<div class='trafficAnalysis'>
  <div class="oLi">
    <ul class="card_List">
      <li :class="{current:btn1}" @click='liclick1($event)' ref='tali'>流量统计</li>
      <li :class="{current:btn2}"@click='liclick2($event)'ref='tali'>分业务的流量统计</li>
      <li :class="{current:btn3}"@click='liclick3($event)' ref='tali'>ONU匹配情况与流量统计</li>
    </ul>
  </div>
  <div class="odiv card_content">
    <div class='rightone' style="display:block" ref='tadiv1'>
        <trafficstatistics  :selectItem='selectItem' :selected='btn1'></trafficstatistics>
    </div>
    <div class='rightone' style="display:none" ref='tadiv2'>
        <userstrafficstatistics  :selectItem='selectItem' :selected='btn2'></userstrafficstatistics>
    </div>
    <div class='rightone' style="display:none" ref='tadiv3'>
        <!-- <poncount  :selectItem='selectItem' :selected='btn3'></poncount> -->
    </div>
  </div>
</div>
</template>

<script >
// import poncount from '@/components/networkResources/PONCount'
import trafficstatistics from '@/components/trafficAnalysis/TrafficStatistics'
import userstrafficstatistics from '@/components/trafficAnalysis/usersTrafficStatistics'
import Bus from '@/assets/Bus.js'
export default {
  data () {
    return {
      btn1:true,
      btn2:false,
      btn3:false,
      selectItem:{},
    }
  },
  components:{
      // poncount,
      trafficstatistics,
      userstrafficstatistics
  },
  methods:{
    liclick1(e){
      this.btn1=true;
      this.btn2=false;
      this.btn3=false;
      //this.btn4=false;
      this.$refs.tadiv1.style.display='block'
      this.$refs.tadiv2.style.display='none'
      this.$refs.tadiv3.style.display='none'
      //this.$refs.odiv4.style.display='none'
    },
    liclick2(e){
      this.btn2=true;
      this.btn1=false;
      this.btn3=false;
      //this.btn4=false;
      this.$refs.tadiv2.style.display='block'
      this.$refs.tadiv1.style.display='none'
      this.$refs.tadiv3.style.display='none'
      //this.$refs.odiv4.style.display='none'
    },
    liclick3(e){
      this.btn3=true;
      this.btn2=false;
      this.btn1=false;
      //this.btn4=false;
      this.$refs.tadiv3.style.display='block'
      this.$refs.tadiv2.style.display='none'
      this.$refs.tadiv1.style.display='none'
      //this.$refs.odiv4.style.display='none'
    }
  },
  mounted(){
    let self = this;
    Bus.$on('getItem',function(data){
        self.selectItem=data;
    })
  }


}
</script>

<style>
.trafficAnalysis{
  width:100%;
  height:100%;
  position: relative;
  margin: 0;
  padding: 0;
}
.oLi{
  height: 30px;
  width:100%;
  position: relative;
}
.card_List{
  display: flex;
  width:60%;
  height:100%;
  position: relative;
  padding: 0;
  margin: 0;
 }
 .card_List li {
  flex:1;
  width:25%;
  text-align: center;
  height: 30px;
  line-height: 30px;
  margin-right: 1px;
  display: block;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background-color:#6A91BB;
  overflow: hidden;
  font-size: 14px;
  color: #fff;
 }
 .card_List li.current {
  height: 34px;
  line-height: 34px;
  margin-top: -4px;
  background-color: #2C58A6;
  color: #fff;
  font-size: 16px;
 }
 .card_content{
   height: calc(100% - 30px);
   border: 1px solid #2C58A6;
 }
.rightone{
    width:100%;
    height: 100%;
    margin-top:0;
}
</style>



