<template>
<div class="vendorCount">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="vendorChart" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'vendorCount',
    data () {
        return {
            msg: '',
            isLoading: false,
            OLTcount:"",
            vendor:''
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
        
    factoryChart() {
        // 基于准备好的dom，初始化echarts实例
        //this.clearChart('vendorChart')
        let myChart = echarts.init(document.getElementById('vendorChart'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            title: {
                text: '总数',
                subtext: this.OLTcount,
                textStyle: {
                    color: '#333',
                    fontSize: 14,
                    // align: 'center'
                },
                subtextStyle: {
                    fontSize: 14,
                    color: ['#333']
                },
                x: 'center',
                y: 'center',
            },
            legend: {
                orient: 'vertical',
                top: "middle",
                right: "right",
                textStyle: {
                    color: '#333',
                    fontSize: 14,

                },
                icon: 'roundRect',
                data: this.vendor,
            },
            series: [
                // 主要展示层的
                {
                    radius: ['20%', '41%'],
                    center: ['50%', '50%'],
                    type: 'pie',
                    label: {
                        normal: {
                            show: true,
                            formatter: "{c}",
                            textStyle: {
                                fontSize: 16,
                            },
                            position: 'outside'
                        },
                        emphasis: {
                            show: true
                        }
                    },
                    labelLine: {
                        normal: {
                            show: true,
                            length: 30,
                            length2: 55
                        },
                        emphasis: {
                            show: true
                        }
                    },
                    data: this.vendor,

                },
                // 边框的设置
                {
                    radius: ['16%', '20%'],
                    center: ['50%', '50%'],
                    type: 'pie',
                    label: {
                        normal: {
                            show: false
                        },
                        emphasis: {
                            show: false
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false
                        },
                        emphasis: {
                            show: false
                        }
                    },
                    animation: false,
                    tooltip: {
                        show: false
                    },
                    data: [{
                        value: 1,
                        itemStyle: {
                            color: "rgba(250,250,250,0.3)",
                        },
                    }],
                }, {
                    name: '外边框',
                    type: 'pie',
                    clockWise: false, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    center: ['50%', '50%'],
                    radius: ['45%', '45%'],
                    label: {
                        normal: {
                            show: false
                        }
                    },
                    data: [{
                        value: 9,
                        name: '',
                        itemStyle: {
                            normal: {
                                borderWidth: 2,
                                borderColor: '#0b5263'
                            }
                        }
                    }]
                },
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createvendordata(data){
        this.vendor=[]
        for (let i=0;i<data.length;i++){
            let temp={
                'name': data[i].brand,
                'value': data[i].olt_count
            }
            this.vendor.push(temp)
        }
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/olt-count/',{
				params:{
                    city:this.city,
                    department:this.department ? this.department.slice(0,-3)+"通信分公司" : this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this.OLTcount=response.data.data.olt_count
            })
            .catch(function (error) {
                console.log(error)
            });
            axios.get('/api/olt-vendor-count/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this.isLoading=false
                this._createvendordata(response.data.data)
                this.factoryChart()
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


