<template>
<div class="modelCount">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="modelChart" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'modelCount',
    data () {
        return {
            msg: '',
            isLoading: false,
            model:"",
            modellist:""
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
    vendorModelChart(){ 
        //this.clearChart('modelChart')
        let myChart = echarts.init(document.getElementById('modelChart'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            legend: [{
                orient: 'vertical', 
                top: "top",
                right: "60%",
                textStyle: {
                    color: '#333',
                    fontSize: 14,
                },
                // tslint:disable-next-line:max-line-length
                data: this.modellist.slice(0,Math.ceil(this.modellist.length/2)),
            },{
                orient: 'vertical', 
                top: "top",
                right: "20%",
                textStyle: {
                    color: '#333',
                    fontSize: 14,
                },
                // tslint:disable-next-line:max-line-length
                data: this.modellist.slice(Math.ceil(this.modellist.length/2)),
            }],
            xAxis : [
                {
                    type : 'category',
                    data : ['各厂家设备型号']
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name :'OLT数量'
                }
            ],
            series: this.model
        };
        myChart.setOption(option);
        window.onresize = function(){
            myChart.resize();
        }
    },
    _createmodeldata(data){
        this.model=[]
        this.modellist=[]
        for (let i=0;i<data.length;i++){
            let temps={
                    name: data[i].brand+data[i].model,
                    type: 'bar',
                    stack: data[i].brand,
                    label: {
                        normal: {
                            show: true,
                            textStyle:{ color:'#fff' }
                        }
                    },
                    data: [data[i].olt_count]
                }
            this.model.push(temps)
            this.modellist.push(data[i].brand+data[i].model)
        }
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/olt-vendor-model-count/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this._createmodeldata(response.data.data)
                this.isLoading=false
                this.vendorModelChart()
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


