<template>
<div class="downstreamTrafficNotMatch">
    <selectFour :search="search">
    </selectFour>
    <div id="downstreamTrafficNotMatch" style="height:500px;margin-top:20px"></div>
    <!-- <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition> -->
</div>
</template>

<script>
import selectFour from "@/components/selectFour"
import Loading from '@/components/loading'
import echarts from 'echarts'
import axios from 'axios'
export default {
    name:"downstreamTrafficNotMatch",
    components:{
        selectFour
    },
methods:{
    search(){
        this.initchart()
    },
    initchart(){
        let myChart = echarts.init(document.getElementById('downstreamTrafficNotMatch'));
        myChart.clear();
        let option = {
            title: {
                text: 'ONU不匹配用户不同带宽下行流量均值-时间曲线'
            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data:["100M","200M","300M","500M"]
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            toolbox: {
                feature: {
                    saveAsImage: {}
                }
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: ['周一','周二','周三','周四','周五','周六','周日']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name:'100M',
                    type:'line',
                    data:[12.9, 32.2,20.8, 34.4, 27.3, 23.8, 21.0]
                },
                {
                    name:'200M',
                    type:'line',
                    data:[22.9, 12.2,10.8, 44.4, 27.3, 43.8, 23.0]
                },
                {
                    name:'300M',
                    type:'line',
                    data:[32.9, 35.2,24.8, 33.4, 23.3, 27.8, 24.0]
                },
                {
                    name:'500M',
                    type:'line',
                    data:[42.9, 42.2,40.8, 44.4, 37.3, 33.8, 51.0]
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
}
}
</script>

<style>

</style>



