import Vue from 'vue'
import Router from 'vue-router'
import helloPage from '@/components/helloPage'
import networkResources from '@/components/networkResources'
import trafficAnalysis from '@/components/trafficAnalysis'
import vendorCount from "@/components/networkResources/vendorCount"
import modelCount from "@/components/networkResources/modelCount"
import oltUsersCount from "@/components/networkResources/oltUsersCount"
import usersStatA from "@/components/networkResources/usersStatA"
import usersStatIPTV from "@/components/networkResources/usersStatIPTV"
import usersStat from "@/components/networkResources/usersStat"
import ponCount from "@/components/networkResources/ponCount"
import serviceBoardStat from "@/components/networkResources/serviceBoardStat"
import ponType from "@/components/networkResources/ponType"
import vendorPonType from "@/components/networkResources/vendorPonType"
import trafficStat from "@/components/networkResources/trafficStat"
import ponUsersStat from "@/components/networkResources/ponUsersStat"
import portCount from "@/components/networkResources/portCount"
import trafficRate from "@/components/networkResources/trafficRate"
import baseInfo from "@/components/networkResources/baseInfo"
import upstreamTraffic from "@/components/trafficAnalysis/upstreamTraffic"
import downstreamTraffic from "@/components/trafficAnalysis/downstreamTraffic"
import averageTrafficRate from "@/components/trafficAnalysis/averageTrafficRate"
import upstreamTrafficPerPackage from "@/components/trafficAnalysis/upstreamTrafficPerPackage"
import downstreamTrafficPerPackage from "@/components/trafficAnalysis/downstreamTrafficPerPackage"
import upstreamTrafficNotMatch from "@/components/trafficAnalysis/upstreamTrafficNotMatch"
import downstreamTrafficNotMatch from "@/components/trafficAnalysis/downstreamTrafficNotMatch"
import upstreamTrafficMatch from "@/components/trafficAnalysis/upstreamTrafficMatch"
import downstreamTrafficMatch from "@/components/trafficAnalysis/downstreamTrafficMatch"
import upstreamTrafficTrends from "@/components/trafficAnalysis/upstreamTrafficTrends"
import downstreamTrafficTrends from "@/components/trafficAnalysis/downstreamTrafficTrends"
import ONUType from "@/components/networkResources/ONUType"
import ONUnotMatch from "@/components/networkResources/ONUnotMatch"
//import mainc from '@/components/mainC'
//import businessModel from '@/components/business-model'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '主页',
      components: {
        homePage:helloPage
      }
    },
    {
      path: '/businessModel',
      name: '业务模型及网络架构',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/userHabits',
      name: '用户行为习惯',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/chartDownload',
      name: '总表下载',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/networkResources',
      name: '网络资源统计',
      components: {
        // mainleft:sidemenu,
        businessModel:networkResources,
      },
      children:[
        { path: '/vendorCount',
          name:'OLT数量及各厂家占比',
          components: {
            appRight:vendorCount
          },
        },
        { path: '/modelCount',
          name:'各型号占比',
          components: {
            appRight:modelCount
          },
        },
        { path: '/oltUsersCount',
          name:'OLT下挂用户分布',
          components: {
            appRight:oltUsersCount
          },
        },
        { path: '/usersStatA',
        name:'分业务带宽的用户数分布',
        components: {
          appRight:usersStatA
        },
      },
        { path: '/usersStatIPTV',
          name:'IPTV用户数分布',
          components: {
            appRight:usersStatIPTV
          },
        },
        { path: '/usersStat',
          name:'分业务类型的用户数分布',
          components: {
            appRight:usersStat
          },
        },
        { path: '/ponCount',
          name:'PON口数量及利用率',
          components: {
            appRight:ponCount
          },
        },
        { path: '/serviceBoardStat',
          name:'业务板槽位利用率分布',
          components: {
            appRight:serviceBoardStat
          },
        },
        { path: '/ponType',
          name:'PON口制式比例',
          components: {
            appRight:ponType
          },
        },
        { path: '/vendorPonType',
          name:'PON口制式厂家比例',
          components: {
            appRight:vendorPonType
          },
        },
        { path: '/trafficStat',
          name:'PON口流量利用率',
          components: {
            appRight:trafficStat
          },
        },
        { path: '/ponUsersStat',
          name:'PON口用户携带占比',
          components: {
            appRight:ponUsersStat
          },
        },
        { path: '/portCount',
          name:'100M与GE与10GE端口数',
          components: {
            appRight:portCount
          },
        },
        { path: '/trafficRate',
          name:'上联口流量分布',
          components: {
            appRight:trafficRate
          },
        },
        { path: '/baseInfo',
          name:'设备信息',
          components: {
            appRight:baseInfo
          },
        },

        { path: '/ONUType',
          name:'三种ONU的比例',
          components: {
            appRight:ONUType
          },
        },
        { path: '/ONUnotMatch',
        name:'ONU不匹配的用户数',
        components: {
          appRight:ONUnotMatch
        },
      },

        { path: '/upstreamTraffic',
          name:'人均上行流量',
          components: {
            appRight:upstreamTraffic
          },
        },
        { path: '/downstreamTraffic',
          name:'人均下行流量',
          components: {
            appRight:downstreamTraffic
          },
        },
        { path: '/averageTrafficRate',
          name:'上下行流量的比例',
          components: {
            appRight:averageTrafficRate
          },
        },
        { path: '/upstreamTrafficPerPackage',
          name:'不同套餐的平均上行流量',
          components: {
            appRight:upstreamTrafficPerPackage
          },
        },
        { path: '/downstreamTrafficPerPackage',
          name:'不同套餐的平均下行流量',
          components: {
            appRight:downstreamTrafficPerPackage
          },
        },
        { path: '/upstreamTrafficNotMatch',
          name:'不匹配用户的平均上行流量',
          components: {
            appRight:upstreamTrafficNotMatch
          },
        },
        { path: '/downstreamTrafficNotMatch',
          name:'不匹配用户的平均下行流量',
          components: {
            appRight:downstreamTrafficNotMatch
          },
        },
        { path: '/upstreamTrafficMatch',
          name:'匹配用户的平均上行流量',
          components: {
            appRight:upstreamTrafficMatch
          },
        },
        { path: '/downstreamTrafficMatch',
          name:'匹配用户的平均下行流量',
          components: {
            appRight:downstreamTrafficMatch
          },
        },
        { path: '/upstreamTrafficTrends',
          name:'人均上行流量变化趋势',
          components: {
            appRight:upstreamTrafficTrends
          },
        },
        { path: '/downstreamTrafficTrends',
          name:'人均下行流量变化趋势',
          components: {
            appRight:downstreamTrafficTrends
          },
        },
      ]
    },
    {
      path: '/trafficCollect',
      name: '业务流量采集',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/trafficAnalysis',
      name: '业务流量分析',
      components: {
        // mainleft:sidemenu,
        businessModel:trafficAnalysis,
      }
    },
    {
      path: '/trafficEstimate',
      name: '业务流量估算',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/capacityAnalysis',
      name: '现网承载能力分析',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/serviceCapacityAnalysis',
      name: '业务对网络承载能力分析',
      components: {
        businessModel:helloPage
      }
    },
    {
      path: '/planEvaluation',
      name: '规划建设方案评估',
      components: {
        businessModel:helloPage
      }
    }
  ]
})



