<template>
<div class="userStatA">
    <selectThree :search="search">
    </selectThree>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="userstatA" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectThree from "@/components/selectThree"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'userStatA',
    data () {
        return {
            msg: '',
            isLoading: false,
            userstatAbar:"",
            userstatApie:""
        }
    },
    components:{
        Loading,
        selectThree
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
        olt(){
            return this.$store.getters.getOLT
        }
    },
    methods:{
    initChart(chart,datapie,databar){
        let myChart = echarts.init(document.getElementById(chart));
        myChart.clear();
        let option = {
            color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {top: '10%',left:"5%",right:"50%"},
            legend: {
                    orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:datapie
                },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : databar[0],
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: "用户数",
                }
            ],
            series : [
                {
                    name:'用户数',
                    type:'bar',
                    data:databar[1],
                },
                { 
                    name:'数量',
                    type:'pie',
                    radius: ['20%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: ' {b|{b}}\n {hr|}\n {c} {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20,
                                    align:'center'
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2
                                }
                            }
                        }
                    },
                    data:datapie
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createUsersStatA(data){
        this.userstatAbar=[]
        this.userstatApie=[]
        let tempx=[]
        let tempy=[]
        for (let i=0;i<data.length;i++){
            // tempx.push(data[i].package_speed)
            // tempy.push(data[i].cnt)
            let temppie={
                'name': data[i].package_speed,
                'value': data[i].cnt
            }
            this.userstatApie.push(temppie)
        }
        let datalist = this.userstatApie.sort((a, b) => a.value - b.value);
         for (let i=0;i<datalist.length;i++){
            tempx.push(datalist[i].name)
            tempy.push(datalist[i].value)
        }
        this.userstatAbar.push(tempx)
        this.userstatAbar.push(tempy)
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/pon-port-users-stat/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station,
                    olt:this.olt
				}
			})
            .then((response) =>{
                this._createUsersStatA(response.data.data.count_list)
                this.isLoading=false
                this.initChart('userstatA',this.userstatApie,this.userstatAbar)
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


