<template>
  <div class="rightOne">
      <div class='mainbody'>
          <div class='btns'>
            <button type="button" class='btn' :class="{btnactive:btn1}" @click="click1($event)" ref="btn1">不同套餐下的平均流量</button>
            <button type="button" class='btn' :class="{btnactive:btn2}" @click="click2($event)" ref="btn2">平均流量人数分布</button>
          </div>
          <div id="userstschart1" ref='userstschart1' class='mChart' style="dispaly:block"></div>
          <div id="userstschart2" ref='userstschart2' class='mChart' style='display:none'></div>
          <div id='userstschart3' ref='userstschart3' class='mChart' style='display:none'>
            <p>该层级无相关统计信息</p>
        </div>
      </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import Bus from '@/assets/Bus.js'
import axios from "axios"
export default {
  data () {
    return {
        selectBtn:'btn1',
        btn1:true,
        btn2:false,
        userstspie1:[{value:78, name:'上行流量'},
                {value:22, name:'下行流量'}],
        userstsbar1:[['上行流量','下行流量'],[78,22]],
        userstspie2:[{value:69, name:'上行平均流量'},
                {value:31, name:'下行平均流量'}],
        userstsbar2:[['上行平均流量','下行平均流量'],[69,31]]
        
    }
  },
  props:{
      selectItem:{},
      selected:false
  },
  watch:{
      selectItem:function(newval,oldval){
          if (this.selected){
            if(this.selectBtn=='btn1'){
                this.userstscount1(this.selectItem)
            }
            if(this.selectBtn=='btn2'){
                this.userstscount2(this.selectItem)
            }
          }
      },
      selected:function(newval,oldval){
          if (newval){
            if(this.selectBtn=='btn1'){
                this.userstscount1(this.selectItem)
            }
            if(this.selectBtn=='btn2'){
                this.userstscount2(this.selectItem)
            }
          }
      }
  },
  methods:{
    userstschart1(self){
        let myChart = echarts.init(document.getElementById('userstschart1'));
        myChart.clear();

        var dataMap = {};
        function dataFormatter(obj) {
            var pList = ['上行流量','下行流量'];
            var temp;
            for (var year = 2002; year <= 2008; year++) {
                var max = 0;
                var sum = 0;
                temp = obj[year];
                for (var i = 0, l = temp.length; i < l; i++) {
                    max = Math.max(max, temp[i]);
                    sum += temp[i];
                    obj[year][i] = {
                        name : pList[i],
                        value : temp[i]
                    }
                }
                obj[year + 'max'] = Math.floor(max / 100) * 100;
                obj[year + 'sum'] = sum;
            }
            return obj;
        }
        dataMap.dataGDP = dataFormatter({
            //max : 60000,
            2008:[1300,2000],
            2007:[1400,1000],
            2006:[1500,1200],
            2005:[1100,2000],
            2004:[1200,1600],
            2003:[1400,1800],
            2002:[1700,2000],
        });
        let option = {
            baseOption: {
                color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85', "rgba(250,250,250,0.3)"],
                backgroundColor: '#fff',
                timeline: {
                    // y: 0,
                    axisType: 'category',
                    // realtime: false,
                    // loop: false,
                    autoPlay: true,
                    // currentIndex: 2,
                    playInterval: 2000,
                    // controlStyle: {
                    //     position: 'left'
                    // },
                    data: [
                        '20M','30M','50M','100M','200M','300M','500M'
                    ],
                },
                legend: {
                    x: 'right',
                    data: ['流量'],
                },
                calculable : true,
                grid: {
                    top: 80,
                    bottom: 100,
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow',
                            label: {
                                show: true,
                                formatter: function (params) {
                                    return params.value.replace('\n', '');
                                }
                            }
                        }
                    }
                },
                xAxis: [
                    {
                        'type':'category',
                        'axisLabel':{'interval':0},
                        'data':[
                            '上行流量','下行流量'
                        ],
                        splitLine: {show: false}
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        name: '平均流量'
                    }
                ],
                series: [
                    {name: '流量', type: 'bar'},
                ]
            },
            options: [
                {
                    title: {text: '20M套餐'},
                    series: [
                        {data: dataMap.dataGDP['2002']},
                    ]
                },
                {
                    title : {text: '30M套餐'},
                    series : [
                        {data: dataMap.dataGDP['2003']},
                    ]
                },
                {
                    title : {text: '50M套餐'},
                    series : [
                        {data: dataMap.dataGDP['2004']},
                    ]
                },
                {
                    title : {text: '100M套餐'},
                    series : [
                        {data: dataMap.dataGDP['2005']},
                    ]
                },
                {
                    title : {text: '200M套餐'},
                    series : [
                        {data: dataMap.dataGDP['2006']},
                    ]
                },
                {
                    title : {text: '300M套餐'},
                    series : [
                        {data: dataMap.dataGDP['2007']},
                    ]
                },
                {
                    title : {text: '500M套餐'},
                    series : [
                        {data: dataMap.dataGDP['2008']},
                    ]
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    userstschart2(datapie,databar){
        let myChart = echarts.init(document.getElementById('userstschart2'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85', "rgba(250,250,250,0.3)"],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                    myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
                        show:true,//是否显示    
                        title:'切换饼图', //鼠标移动上去显示的文字    
                        icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
                        onclick:function() {//点击事件,这里的option1是chart的option信息    
                            myChart.clear();
                            let option2 = {
                                color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85', "rgba(250,250,250,0.3)"],
                                backgroundColor: '#fff',
                                tooltip: {
                                    trigger: 'item',
                                    formatter: "{b}<br/>{c} ({d}%)"
                                },
                                legend: {
                                    left: 'center',
                                    top:'bottom',
                                    data:datapie
                                },
                                toolbox: {
                                        feature: {
                                            saveAsImage: {show: true,backgroundColor: '#fff'},
                                            myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
                                                show:true,//是否显示    
                                                title:'切换柱状图', //鼠标移动上去显示的文字    
                                                icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
                                                onclick:function() {//点击事件,这里的option1是chart的option信息    
                                                    myChart.clear();
                                                    myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
                                                }
                                            }
                                        }
                                    },
                                series: [
                                    {
                                        name:'流量',
                                        type:'pie',
                                        radius: ['20%', '35%'],
                                        label: {
                                            normal: {
                                                formatter: '{b|{b}}\n {hr|}\n {c} {per|{d}%}  ',
                                                backgroundColor: '#eee',
                                                borderColor: '#aaa',
                                                borderWidth: 1,
                                                borderRadius: 4,
                                                rich: {
                                                    hr: {
                                                        borderColor: '#aaa',
                                                        width: '100%',
                                                        borderWidth: 0.5,
                                                        height: 0
                                                    },
                                                    b: {
                                                        fontSize: 14,
                                                        lineHeight: 20,
                                                        align: 'center'
                                                    },
                                                    per: {
                                                        color: '#eee',
                                                        backgroundColor: '#334455',
                                                        padding: [2, 4],
                                                        borderRadius: 2
                                                    }
                                                }
                                            }
                                        },
                                        data:datapie
                                    }
                                ]
                            };
                            myChart.setOption(option2);
                        }    
                   },
                   
                }
            },
            legend: {
                data:
                    [{name:'流量',textStyle:{fontWeight:'bold', color:'black'}}],

            },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : databar[0],

                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: '流量'
                }
            ],
            series : [
                {
                    name:'流量',
                    type:'bar',
                    data:databar[1],
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    click1(e){
        this.$refs.userstschart1.style.display='block'
        this.$refs.userstschart2.style.display='none'
        this.$refs.userstschart3.style.display='none'
        this.btn1=true
        this.btn2=false
        this.selectBtn='btn1'
        this.userstscount1(this.selectItem)

    },
    click2(e){
        this.$refs.userstschart2.style.display='block'
        this.$refs.userstschart1.style.display='none'
        this.$refs.userstschart3.style.display='none'
        this.btn1=false
        this.btn2=true
        this.selectBtn='btn2'
        this.userstscount2(this.selectItem)
    },
    _createPonCount(data){
        this.portratepie=[]
        this.portratebar=[]
        let tempx=[]
        let tempy=[]
        for (let i=0;i<data.length;i++){
            let temp={
                'name': data[i].bandwidth,
                'value': data[i].cnt
            }
            this.portratepie.push(temp)
            tempx.push(data[i].bandwidth)
            tempy.push(data[i].cnt)
        }
        this.portratebar.push(tempx)
        this.portratebar.push(tempy)
    },
    _createTrafficRate(data){
        this.trafficratepie=[]
        this.trafficratebar=[]
        this.trafficratepie=[{
            name:'0-500Mbps',
            value:data['0-500Mbps']
        },{
            name:'500-1000Mbps',
            value:data['500-1000Mbps']
        },{
            name:'1000-1500Mbps',
            value:data['1000-1500Mbps']
        },{
            name:'1500-2000Mbps',
            value:data['1500-2000Mbps']
        },{
            name:'2000Mbps+',
            value:data['2000Mbps+']
        }]
        let tempx=['0-500Mbps','500-1000Mbps','1000-1500Mbps','1500-2000Mbps','2000Mbps+']
        let tempy=[data['0-500Mbps'],data['500-1000Mbps'],data['1000-1500Mbps'],data['1500-2000Mbps'],data['2000Mbps+']]
        this.trafficratebar.push(tempx)
        this.trafficratebar.push(tempy)
    },
    userstscount1(data){
        if(!data.level){
            this.userstschart1(this)
        }
        if (data.level==1){
            //天津
            this.$refs.userstschart1.style.display='block'
            this.$refs.userstschart3.style.display='none'
            console.log('userstscount1')
            this.userstschart1(this)
        }
        if (data.level==2){
            //分公司
            this.$refs.userstschart1.style.display='block'
            this.$refs.userstschart3.style.display='none'
            this.userstschart1(this)
        }
        if (data.level==3){
            //局站
            this.$refs.userstschart1.style.display='block'
            this.$refs.userstschart3.style.display='none'
            this.userstschart1(this)
        }
        if(data.level==4){
            this.$refs.userstschart3.style.display='block'
            this.$refs.userstschart1.style.display='none'
        }

    },
    userstscount2(data){
        if (!data.level){
            this.userstschart2(this.userstspie2,this.userstsbar2)
        }
        if (data.level==1){
            this.$refs.userstschart2.style.display='block'
            this.$refs.userstschart3.style.display='none'
            this.userstschart2(this.userstspie2,this.userstsbar2)
        }
        if (data.level==2){
            this.$refs.userstschart2.style.display='block'
            this.$refs.userstschart3.style.display='none'
            this.userstschart2(this.userstspie2,this.userstsbar2)
        }
        if (data.level==3){
            this.$refs.userstschart2.style.display='block'
            this.$refs.userstschart3.style.display='none'
            this.userstschart2(this.userstspie2,this.userstsbar2)
        }
        if (data.level==4){
            this.$refs.userstschart3.style.display='block'
            this.$refs.userstschart2.style.display='none'
        }
        
    },

  },
  mounted(){
        if(this.selectBtn=='btn1'){
            this.userstscount1(this.selectItem)
        }
        if(this.selectBtn=='btn2'){
            this.userstscount2(this.selectItem)
        }
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rightOne{
    width:100%;
    height:100%;
    position: relative;
}
.mainbody{
    width:100%;
    height:calc(100% - 20px);
    margin-top:20px;
    display: flex;
    position: relative;
}
.btns{
    width: 100px;
    height:100%;
    margin-left:10px;
    display:flex;
    position: relative;
    flex-direction: column;
}
.btn {
    border-radius: 4px;
    width:90%;
    height:45px;
    background-color:#2C58A6;
    color: white;
    font-size:12px;
    margin-bottom: 10px;
    position: relative;
}
.mChart{
    width:calc(100% - 110px);
    flex: 1;
    height:100%;
    position: relative;
}
.btnactive{
    background-color:#6A91BB;
}
</style>


