<template>
  <div class="rightOne">
      <div class='mainbody'>
          <div class='btns'>
            <button type="button" class='btn' :class="{btnactive:btn1}" @click="click1($event)" ref="btn1">上下行流量比</button>
            <button type="button" class='btn' :class="{btnactive:btn2}" @click="click2($event)" ref="btn2">平均流量</button>
          </div>
          <div id="tschart1" ref='tschart1' class='mChart' style="dispaly:block"></div>
          <div id="tschart2" ref='tschart2' class='mChart' style='display:none'></div>
          <div id='tschart3' ref='tschart3' class='mChart' style='display:none'>
            <p>该层级无相关统计信息</p>
        </div>
      </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import Bus from '@/assets/Bus.js'
import axios from "axios"
export default {
  data () {
    return {
        selectBtn:'btn1',
        btn1:true,
        btn2:false,
        tspie1:[{value:78, name:'上行流量'},
                {value:22, name:'下行流量'}],
        tsbar1:[['上行流量','下行流量'],[78,22]],
        tspie2:[{value:69, name:'上行平均流量'},
                {value:31, name:'下行平均流量'}],
        tsbar2:[['上行平均流量','下行平均流量'],[69,31]]
        
    }
  },
  props:{
      selectItem:{},
      selected:false
  },
  watch:{
      selectItem:function(newval,oldval){
          if (this.selected){
            if(this.selectBtn=='btn1'){
                this.tscount1(this.selectItem)
            }
            if(this.selectBtn=='btn2'){
                this.tscount2(this.selectItem)
            }
          }
      },
      selected:function(newval,oldval){
          if (newval){
            if(this.selectBtn=='btn1'){
                this.tscount1(this.selectItem)
            }
            if(this.selectBtn=='btn2'){
                this.tscount2(this.selectItem)
            }
          }
      }
  },
  methods:{
    tschart1(self){
        let myChart = echarts.init(document.getElementById('tschart1'));
        myChart.clear();
        let option = {
            color: ['#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                    myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
                        show:true,//是否显示    
                        title:'切换饼图', //鼠标移动上去显示的文字    
                        icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
                        onclick:function() {//点击事件,这里的option1是chart的option信息    
                            myChart.clear();
                            let option2 = {
                                color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
                                backgroundColor: '#fff',
                                tooltip: {
                                    trigger: 'item',
                                    formatter: "{b} <br/>{c} ({d}%)"
                                },
                                legend: {
                                    left: 'center',
                                    top:"bottom",
                                    data:self.tspie1
                                },
                                toolbox: {
                                        feature: {
                                            saveAsImage: {show: true,backgroundColor: '#fff'},
                                            myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
                                                show:true,//是否显示    
                                                title:'切换柱状图', //鼠标移动上去显示的文字    
                                                icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
                                                onclick:function() {//点击事件,这里的option1是chart的option信息    
                                                    myChart.clear();
                                                    myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
                                                }
                                            }
                                        }
                                    },
                                series: [
                                    {
                                        name:'流量',
                                        type:'pie',
                                        radius: ['20%', '35%'],
                                        label: {
                                            normal: {
                                                formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
                                                backgroundColor: '#eee',
                                                borderColor: '#aaa',
                                                borderWidth: 1,
                                                borderRadius: 4,
                                                rich: {
                                                    a: {
                                                        color: '#999',
                                                        lineHeight: 18,
                                                        align: 'center'
                                                    },
                                                    hr: {
                                                        borderColor: '#aaa',
                                                        width: '100%',
                                                        borderWidth: 0.5,
                                                        height: 0
                                                    },
                                                    b: {
                                                        fontSize: 14,
                                                        lineHeight: 20
                                                    },
                                                    per: {
                                                        color: '#eee',
                                                        backgroundColor: '#334455',
                                                        padding: [2, 4],
                                                        borderRadius: 2
                                                    }
                                                }
                                            }
                                        },
                                        data:self.tspie1
                                    }
                                ]
                            };
                            myChart.setOption(option2);
                        }    
                   },
                   
                }
            },
            legend: {
                data:
                    [{name:'流量',textStyle:{fontWeight:'bold', color:'black'}}],

            },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : self.tsbar1[0],
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: '流量',
                }
            ],
            series : [
                {
                    name:'流量',
                    type:'bar',
                    data:self.tsbar1[1],
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    tschart2(datapie,databar){
        let myChart = echarts.init(document.getElementById('tschart2'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85', "rgba(250,250,250,0.3)"],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                    myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
                        show:true,//是否显示    
                        title:'切换饼图', //鼠标移动上去显示的文字    
                        icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
                        onclick:function() {//点击事件,这里的option1是chart的option信息    
                            myChart.clear();
                            let option2 = {
                                color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85', "rgba(250,250,250,0.3)"],
                                backgroundColor: '#fff',
                                tooltip: {
                                    trigger: 'item',
                                    formatter: "{b}<br/>{c} ({d}%)"
                                },
                                legend: {
                                    left: 'center',
                                    top:'bottom',
                                    data:datapie
                                },
                                toolbox: {
                                        feature: {
                                            saveAsImage: {show: true,backgroundColor: '#fff'},
                                            myTool2:{//自定义按钮 danielinbiti,这里增加，thisbuttons可以随便取名字    
                                                show:true,//是否显示    
                                                title:'切换柱状图', //鼠标移动上去显示的文字    
                                                icon:'image://http://echarts.baidu.com/images/favicon.png', //图标    
                                                onclick:function() {//点击事件,这里的option1是chart的option信息    
                                                    myChart.clear();
                                                    myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
                                                }
                                            }
                                        }
                                    },
                                series: [
                                    {
                                        name:'流量',
                                        type:'pie',
                                        radius: ['20%', '35%'],
                                        label: {
                                            normal: {
                                                formatter: '{b|{b}}\n {hr|}\n {c} {per|{d}%}  ',
                                                backgroundColor: '#eee',
                                                borderColor: '#aaa',
                                                borderWidth: 1,
                                                borderRadius: 4,
                                                rich: {
                                                    hr: {
                                                        borderColor: '#aaa',
                                                        width: '100%',
                                                        borderWidth: 0.5,
                                                        height: 0
                                                    },
                                                    b: {
                                                        fontSize: 14,
                                                        lineHeight: 20,
                                                        align: 'center'
                                                    },
                                                    per: {
                                                        color: '#eee',
                                                        backgroundColor: '#334455',
                                                        padding: [2, 4],
                                                        borderRadius: 2
                                                    }
                                                }
                                            }
                                        },
                                        data:datapie
                                    }
                                ]
                            };
                            myChart.setOption(option2);
                        }    
                   },
                   
                }
            },
            legend: {
                data:
                    [{name:'流量',textStyle:{fontWeight:'bold', color:'black'}}],

            },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : databar[0],

                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: '流量'
                }
            ],
            series : [
                {
                    name:'流量',
                    type:'bar',
                    data:databar[1],
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    click1(e){
        this.$refs.tschart1.style.display='block'
        this.$refs.tschart2.style.display='none'
        this.$refs.tschart3.style.display='none'
        this.btn1=true
        this.btn2=false
        this.selectBtn='btn1'
        this.tscount1(this.selectItem)

    },
    click2(e){
        this.$refs.tschart2.style.display='block'
        this.$refs.tschart1.style.display='none'
        this.$refs.tschart3.style.display='none'
        this.btn1=false
        this.btn2=true
        this.selectBtn='btn2'
        this.tscount2(this.selectItem)
    },
    _createPonCount(data){
        this.portratepie=[]
        this.portratebar=[]
        let tempx=[]
        let tempy=[]
        for (let i=0;i<data.length;i++){
            let temp={
                'name': data[i].bandwidth,
                'value': data[i].cnt
            }
            this.portratepie.push(temp)
            tempx.push(data[i].bandwidth)
            tempy.push(data[i].cnt)
        }
        this.portratebar.push(tempx)
        this.portratebar.push(tempy)
    },
    _createTrafficRate(data){
        this.trafficratepie=[]
        this.trafficratebar=[]
        this.trafficratepie=[{
            name:'0-500Mbps',
            value:data['0-500Mbps']
        },{
            name:'500-1000Mbps',
            value:data['500-1000Mbps']
        },{
            name:'1000-1500Mbps',
            value:data['1000-1500Mbps']
        },{
            name:'1500-2000Mbps',
            value:data['1500-2000Mbps']
        },{
            name:'2000Mbps+',
            value:data['2000Mbps+']
        }]
        let tempx=['0-500Mbps','500-1000Mbps','1000-1500Mbps','1500-2000Mbps','2000Mbps+']
        let tempy=[data['0-500Mbps'],data['500-1000Mbps'],data['1000-1500Mbps'],data['1500-2000Mbps'],data['2000Mbps+']]
        this.trafficratebar.push(tempx)
        this.trafficratebar.push(tempy)
    },
    tscount1(data){
        if(!data.level){
            this.tschart1(this)
        }
        if (data.level==1){
            //天津
            this.$refs.tschart1.style.display='block'
            this.$refs.tschart3.style.display='none'
            console.log('tscount1')
            this.tschart1(this)
        }
        if (data.level==2){
            //分公司
            this.$refs.tschart1.style.display='block'
            this.$refs.tschart3.style.display='none'
            this.tschart1(this)
        }
        if (data.level==3){
            //局站
            this.$refs.tschart1.style.display='block'
            this.$refs.tschart3.style.display='none'
            this.tschart1(this)
        }
        if(data.level==4){
            this.$refs.tschart3.style.display='block'
            this.$refs.tschart1.style.display='none'
        }

    },
    tscount2(data){
        if (!data.level){
            this.tschart2(this.tspie2,this.tsbar2)
        }
        if (data.level==1){
            this.$refs.tschart2.style.display='block'
            this.$refs.tschart3.style.display='none'
            this.tschart2(this.tspie2,this.tsbar2)
        }
        if (data.level==2){
            this.$refs.tschart2.style.display='block'
            this.$refs.tschart3.style.display='none'
            this.tschart2(this.tspie2,this.tsbar2)
        }
        if (data.level==3){
            this.$refs.tschart2.style.display='block'
            this.$refs.tschart3.style.display='none'
            this.tschart2(this.tspie2,this.tsbar2)
        }
        if (data.level==4){
            this.$refs.tschart3.style.display='block'
            this.$refs.tschart2.style.display='none'
        }
        
    },

  },
  mounted(){
        if(this.selectBtn=='btn1'){
            this.tscount1(this.selectItem)
        }
        if(this.selectBtn=='btn2'){
            this.tscount2(this.selectItem)
        }
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rightOne{
    width:100%;
    height:100%;
    position: relative;
}
.mainbody{
    width:100%;
    height:calc(100% - 20px);
    margin-top:20px;
    display: flex;
    position: relative;
}
.btns{
    width: 100px;
    height:100%;
    margin-left:10px;
    display:flex;
    position: relative;
    flex-direction: column;
}
.btn {
    border-radius: 4px;
    width:90%;
    height:45px;
    background-color:#2C58A6;
    color: white;
    font-size:12px;
    margin-bottom: 10px;
    position: relative;
}
.mChart{
    width:calc(100% - 110px);
    flex: 1;
    height:100%;
    position: relative;
}
.btnactive{
    background-color:#6A91BB;
}
</style>


