<template>
<div class="modelCount">
    <selectThree :search="search">
    </selectThree>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="infochart1" style="height:500px;margin-top:20px">
              <p>当前设备为{{city}}—{{department}}—{{station}}</p>
              <p>设备制式为EPON</p>
              <p>所在局站为{{city}}—{{department}}</p>
              <p>上联口类型和数量</p>
              
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectThree from "@/components/selectThree"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'modelCount',
    data () {
        return {
            msg: '',
            isLoading: false,
            model:"",
            modellist:""
        }
    },
    components:{
        Loading,
        selectThree
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
        search(){
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

