<template>
<div class="ONUnotMatch">
    <selectThree :search="search">
    </selectThree>
    <div id="onuchart1" style="height:500px;margin-top:20px"></div>
    <!-- <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition> -->
</div>
</template>

<script>
import selectThree from "@/components/selectThree"
import Loading from '@/components/loading'
import echarts from 'echarts'
import axios from 'axios'
export default {
    name:"ONUnotMatch",
    components:{
        selectThree
    },
    data(){
    return {
        databar:"",
        datapie:""
    }
    },
    methods:{
    search(){
        let data=[{
            name:"匹配",
            cnt:56,
        },{
            name:"不匹配",
            cnt:36,
        }]
        this._createUsersStatA(data)
    },
    initchart(databar,datapie){
        let myChart = echarts.init(document.getElementById('onuchart1'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : databar[0],
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: "用户数",
                }
            ],
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            legend: {
                    orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:datapie
                },
            grid: {top: '10%',left:"5%",right:"50%"},
            series : [
                {
                    name:'柱状图',
                    type:'bar',
                    data:datapie,
                },
                {
                    name:'饼图',
                    type:'pie',
                    radius: ['20%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: ' {b|{b}}\n {hr|}\n {c} {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20,
                                    align:'center'
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2
                                }
                            }
                        }
                    },
                    data:datapie
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
        window.onresize = function(){
            myChart.resize();
        }
        // let myChart2 = echarts.init(document.getElementById('tchart2'));
        // myChart2.clear();
    },
    _createUsersStatA(data){
        this.databar=[]
        this.datapie=[]
        let tempx=[]
        let tempy=[]
        for (let i = 0;i<data.length;i++){
            // tempx.push(data[i].name)
            // tempy.push(data[i].cnt)
            let temppie={
                'name': data[i].name,
                'value': data[i].cnt,
                'rate':data[i].cnt/2,
            }
            this.datapie.push(temppie)
        }
        let datalist = this.datapie.sort((a, b) => a.value - b.value);
         for (let i=0;i<datalist.length;i++){
            tempx.push(datalist[i].name)
            tempy.push(datalist[i].value)
        }
        this.databar.push(tempx)
        this.databar.push(tempy)
        this.initchart(this.databar,this.datapie)
    },
}
}
</script>

<style>

</style>



