<template>
<div class="oltUserCount">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="oltUserChart" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'oltUserCount',
    data () {
        return {
            msg: '',
            isLoading: false,
            usercountpie:"",
            usercountbar:""
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
    oltUsersChart(self){
        //this.clearChart('OLTUsers')
        let myChart = echarts.init(document.getElementById('oltUserChart'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0', '#A0CE3A','#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85','#CD8162','#CDC9A5','#CD6600','#DAA520','#6B8E23','#9F79EE'],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'}
                }
            },
            legend: {
                        orient: 'vertical',
                        x: 'right',
                        y:'center',
                        data:self.usercountpie
                    },
            grid: {top: '10%',left:"5%",right:"50%"},
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : this.usercountbar[0],
                    name :'用户数区间',
                    nameLocation :'middle',
                    nameGap: 20,
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: 'OLT数量',
                }
            ],
            series : [
                {
                    name:'数量',
                    type:'bar',
                    data:self.usercountbar[1],
                },
                {
                    name:'用户数区间',
                    type:'pie',
                    radius: ['20%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: ' {b|{b}}\n {hr|}\n {c|{c}} {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20,
                                    height:20,
                                    align:'center'
                                },
                                c:{
                                    fontSize: 14,
                                    lineHeight: 20,
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2,
                                }
                            }
                        }
                    },
                    data:self.usercountpie
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createoltusersdata(data){
        this.usercountpie=[]
        this.usercountbar=[]
        this.usercountpie=[{
            name:'【0-20】',
            value:data['0-20']
        },{
            name:'【20-40】',
            value:data['20-40']
        },{
            name:'【40-60】',
            value:data['40-60']
        },{
            name:'【60-80】',
            value:data['60-80']
        },{
            name:'【80+】',
            value:data['80+']
        }]
        let tempx=['0-20','20-40','40-60','60-80','80+']
        let tempy=[data['0-20'],data['20-40'],data['40-60'],data['60-80'],data['80+']]
        this.usercountbar.push(tempx)
        this.usercountbar.push(tempy)
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/olt-user-count/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this._createoltusersdata(response.data.data)
                this.isLoading=false
                this.oltUsersChart(this)
            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


