<template>
<div class="trafficRate">
    <selectTwo :search="search">
    </selectTwo>
    <transition name="fade">
    <loading v-if="isLoading"></loading>
    </transition>
    <div id="upperchart2" style="height:500px;margin-top:20px">
    </div>
</div>
</template>

<script>
import Loading from '@/components/loading'
import selectTwo from "@/components/selectTwo"
import echarts from 'echarts'
import axios from 'axios'
export default {
    name: 'trafficRate',
    data () {
        return {
            msg: '',
            isLoading: false,
            trafficratepie:[],
        trafficratebar:[]
        }
    },
    components:{
        Loading,
        selectTwo
    },
    computed:{
        city(){
           return  this.$store.getters.getCity
        },
        department(){
           return  this.$store.getters.getDepartment
        },
        station(){
           return  this.$store.getters.getStation
        },
    },
    methods:{
    initChart(datapie,databar){
        let myChart = echarts.init(document.getElementById('upperchart2'));
        myChart.clear();
        let option = {
            color: [ '#31C5C0','#A0CE3A', '#1E9BD1', '#0F347B', '#585247', '#7F6AAD', '#009D85', "rgba(250,250,250,0.3)"],
            backgroundColor: '#fff',
            tooltip : {
                trigger: 'axis'
            },
            toolbox: {
                feature: {
                    saveAsImage: {show: true,backgroundColor: '#fff'},
                }
            },
            grid: {top: '10%',left:"5%",right:"50%"},
            legend: {
                    orient: 'vertical',
                    x: 'right',
                    y:'center',
                    data:datapie
                },
            calculable : true,
            xAxis : [
                {
                    type : 'category',
                    data : databar[0],

                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name: 'OLT数'
                }
            ],
            series : [
                {
                    name:'OLT数',
                    type:'bar',
                    data:databar[1],
                },
                {
                    name:'OLT数',
                    type:'pie',
                    radius: ['20%', '35%'],
                    center: ['75%', '50%'],
                    label: {
                        normal: {
                            formatter: '{b|{b}}\n {hr|}\n {c} {per|{d}%}  ',
                            backgroundColor: '#eee',
                            borderColor: '#aaa',
                            borderWidth: 1,
                            borderRadius: 4,
                            rich: {
                                hr: {
                                    borderColor: '#aaa',
                                    width: '100%',
                                    borderWidth: 0.5,
                                    height: 0
                                },
                                b: {
                                    fontSize: 14,
                                    lineHeight: 20,
                                    align: 'center'
                                },
                                per: {
                                    color: '#eee',
                                    backgroundColor: '#334455',
                                    padding: [2, 4],
                                    borderRadius: 2
                                }
                            }
                        }
                    },
                    data:datapie
                }
            ]
        };
        myChart.setOption(option);window.onresize = function(){
    myChart.resize();
}
    },
    _createTrafficRate(data){
        this.trafficratepie=[]
        this.trafficratebar=[]
        this.trafficratepie=[{
            name:'0-500Mbps',
            value:data['0-500Mbps']
        },{
            name:'500-1000Mbps',
            value:data['500-1000Mbps']
        },{
            name:'1000-1500Mbps',
            value:data['1000-1500Mbps']
        },{
            name:'1500-2000Mbps',
            value:data['1500-2000Mbps']
        },{
            name:'2000Mbps+',
            value:data['2000Mbps+']
        }]
        let tempx=['0-500Mbps','500-1000Mbps','1000-1500Mbps','1500-2000Mbps','2000Mbps+']
        let tempy=[data['0-500Mbps'],data['500-1000Mbps'],data['1000-1500Mbps'],data['1500-2000Mbps'],data['2000Mbps+']]
        this.trafficratebar.push(tempx)
        this.trafficratebar.push(tempy)
    },
        search(){
            this.isLoading=true
            //let _this=this
            axios.get('/api/olt-uplink-stat/',{
				params:{
                    city:this.city,
                    department:this.department,
                    station:this.station
				}
			})
            .then((response) =>{
                this._createTrafficRate(response.data.data)
                this.isLoading=false
                this.initChart(this.trafficratepie,this.trafficratebar)

            })
            .catch(function (error) {
                console.log(error)
            });
            //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
        },

    },
    created(){
        // this.getfirst();
    },
    mounted(){

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


