<template>
  <div id="app">
    <div class='header'>
      <div class='mainTitle'>接入网评估平台</div>
    </div>
    <nav id="mainMenu" >
      <ul id="menus" v-if='$route.path=="/"'>
        <li @mouseover="menuOver($event)" @mouseout="menuOut($event)" v-for="(menu,i) in menus"><a>{{menu.firstMenu}}</a>
            <ul>
              <li v-for="(secondMenu,i) in menu.secondMenu" @click="menuclick(secondMenu,menu.firstMenu)">{{secondMenu.name}}</li>
              <!-- <router-link :to="{name:secondMenu.id}">{{secondMenu.name}}</router-link> -->
            </ul>
        </li>
      </ul>
      <el-breadcrumb v-else style="height:35px;line-height:35px;padding-left:20px;font-weight: bold;font-size:16px" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item style="color：white" :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item style="color：white">{{$route.matched[0].name}}</el-breadcrumb-item>
        <el-breadcrumb-item style="color：white" v-if="$route.matched[1]">{{$route.matched[1].name}}</el-breadcrumb-item>
      </el-breadcrumb>
    </nav>
    <router-view name="homePage" ></router-view>
    <router-view name="businessModel" ></router-view>
    <!-- <div class='mainContent'>
      <div class ='mainleft'>
        <router-view name="mainleft"></router-view>
      </div>
      <div class='mainc'>
        <router-view name='mainc'></router-view>
      </div>
    </div> -->
    <!-- <router-view name="mainbottom"></router-view> -->
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      showmenu:true,
      selectMenu1:"",
      selectMenu2:'',
      menus:[
      {
        firstMenu: "业务模型及网络架构",
        secondMenu:[
          {name:"业务模型及网络架构",id:"businessModel"},
          {name:"用户行为习惯",id:"userHabits"}
        ]
      },{
        firstMenu: "全网资源展示",
        secondMenu:[
          {name:"总表下载",id:"chartDownload"},
          {name:"网络资源统计",id:"networkResources"},
        ]
      },{
        firstMenu: "业务流量采集及分析",
        secondMenu:[
          {name:"业务流量采集",id:"trafficCollect"},
          {name:"业务流量分析",id:"trafficAnalysis"},
          {name:"业务流量估算",id:"trafficEstimate"}
        ]
      },
      {
        firstMenu: "现网承载能力分析",
        secondMenu:[
          {name:"现网承载能力分析",id:"capacityAnalysis"},
          {name:"业务对网络承载能力分析",id:"serviceCapacityAnalysis"}
        ]
      },{
        firstMenu: "规划建设方案评估",
        secondMenu:[
          {name:"规划建设方案评估",id:"planEvaluation"}
        ]
      },
    ]}
  },
  watch:{
    '$route' (to, from){
      console.log(this.$route)
      this.$store.dispatch('getNewMenu',this.$route)
      if (this.$route.path=='/'){
        this.showmenu=true
      }
      else{
        this.showmenu=false
      }
    }
  },
  computed:{

  },
  methods:{
    menuOver(event){
        let ul = event.currentTarget.childNodes[2];
        ul.style.display = "block";
    },
    menuOut(event){
        let ul = event.currentTarget.childNodes[2];
        ul.style.display = "none";
    },
    menuclick(secondmenu,firstmenu){
      this.$router.push(secondmenu.id)
      this.selectMenu2=secondmenu.name
      this.selectMenu1=firstmenu
      // document.getElementById('mainMenu').style.display='none'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin:0;
  padding: 0;
  height:100%;
  width:100%;
  position: relative;
}
.header{
  width: 100%;
  height: 60px;
  text-align: center;
  padding:0;
  background: url(./assets/title.png) no-repeat;
  background-color: #4a6793;
  background-size: 100% 100%;
  color: black;
}
.mainTitle{
  float: center;
  margin-left: 0px;
  font-size: 2em;
  font-weight: bold;
  color: white;
}
#mainMenu{
  width: 100%;
  height: 35px;
  background-color: #4a6793;
  overflow: hidden;
}
#menus {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 35px;
    width: 100%;
    background-color: #4a6793;
}
#menus li {
    float: left;
    width: 15%;
    height: 35px;
    overflow: hidden;
}

#menus li a {
    display: block;
    color: white;
    font-size: bold;
    text-align: center;
    line-height: 35px;
    text-decoration: none;
    overflow: hidden;
    width: 100%;
    height:35px;
}
#menus li ul {
    line-height: 35px;
    list-style:none;
    text-align:center;
    width: 15%;
    height:35px;
    position: absolute;
    z-index:2;
    display:none;
    margin: 0;
    padding: 0;
}
#menus li ul li{
    margin: 0;
    padding: 0;
    width: 100%;
    height:35px;
    border-bottom: solid 1px #f1f1f1;
    overflow: hidden;
    text-align:center;
    background-color: rgb(145, 172, 214);
    color: #333;
    font-weight: bold;
}
#menus li ul li:last-child{
    border-bottom: none;
}
#menus li ul li:hover {background:#6b98e6}
/* .mainContent {
  width: 96%;
  margin-top:40px;
  margin-left:2%;
  margin-right:2%;
  height: 700px;
  display: flex;
  position: relative;
}
.mainleft{
  width: 18%;
  height: 680px;
  position: relative;
}
.mainc{
    width:80%;
    height:680px;
    padding-left:1%;
    position: relative;
    flex:1
} */
.el-submenu__title{
  padding-left: 0;

}
.el-breadcrumb__inner {
    color:white;
}
.el-breadcrumb__inner.is-link{
    color:white;
}
.el-breadcrumb__item:last-child .el-breadcrumb__inner{
  color:white;
}
</style>



